import { isString } from 'lodash-es'
import useOption from '@/configs/useOption/index.js'

/**
 * 获取本地数据字典
 * @param {String} key
 * @returns Array
 **/
const getDict = (key = 'tagList') => {
    return useOption()[key] || [];
};

/**
 * 获取数据字典名称
 * @param source 源数据
 * @param value 值
 * @param 数据字典字段名对象
 **/
const getDictLabel = (source, value, {
    labelKey = 'label',
    valueKey = 'value',
    childrenKey = 'children',
} = {}) => {
    const data = isString(source) ? getDict(source) : source;
    const actions = []
    for (let index = 0; index < data.length; index++) {
        const item = data[index]
        if (item[valueKey] == `${value}`) {
            actions.push(item[labelKey])
            break
        }
        const children = item[childrenKey]
        if (children && children.length) {
            const action = getDictLabel(children, value, {
                labelKey,
                valueKey,
                childrenKey,
            })
            if (action) {
                actions.push(action)
                break
            }
        }
    }
    return Object.prototype.toString.call(actions[0]) === '[object Object]' ? actions[0] : actions.join('')
};
export default { getDict, getDictLabel };
